input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
    font-family: inherit;
    border-style: solid;
    border-width: thin;
}

input:-webkit-autofill {
    background-color: inherit !important;
    color: inherit !important;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="search"] {
    text-transform: lowercase;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="password"] {
    &::placeholder {
        text-transform: lowercase;
    }
}

// textarea {}
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
    border: none;
    border-radius: 0;
    box-shadow: none;
    width: auto;
    padding: $button_padding;
    font: normal none $button_font_weight #{$button_font_size}/#{$button_line_height} $button_font_family;
    background-color: $button_background_color;
    color: $button_color;
}

textarea {
	width: 100%;
}

/*================ Prevent zoom on touch devices in active inputs ================*/

@include at-query($max, $bp_tablet) {
    input,
    textarea {
        font-size: em(16px);
    }
}


input,
textarea,
button,
select {
    padding: 0;
    margin: 0;
    user-select: text;
}

button {
    background: none;
    border: none;
    cursor: pointer;
    width: auto;
    width: auto;
    padding: $button_padding;
    font: normal none $button_font_weight #{$button_font_size}/#{$button_line_height} $button_font_family;
    background-color: $button_background_color;
    color: $button_color;
}

button,
input,
textarea {
    appearance: none;
}

input[type="image"] {
    padding-left: 0;
    padding-right: 0;
}

fieldset {
    border: 1px solid $border_color;
    padding: $gutter / 2;

    ul {
        list-style: none;
    }
}

legend {
    border: 0;
    padding: 0;
}

button,
input[type="submit"] {
    cursor: pointer;
}



/*================ Input element overrides ================*/

input[type="checkbox"],
input[type="radio"] {
    display: inline;
    margin: 0 8px 0 0;
    padding: 0;
    width: auto;
}

input[type="checkbox"] {
    appearance: checkbox;
}

input[type="radio"] {
    appearance: radio;
}

input[type="image"] {
    padding-left: 0;
    padding-right: 0;
}
