/* Selections */
/**
 * Breakpoints
 * @type {[type]}
 */
.show {
  display: block !important;
}

.hide {
  display: none !important;
}

@media only screen and (max-width: 768px) {
  .tablet-down--show {
    display: block !important;
  }
  .tablet-down--hide {
    display: none !important;
  }
}

@media only screen and (min-width: 769px) {
  .desktop--show {
    display: block !important;
  }
  .desktop--hide {
    display: none !important;
  }
}

