/*============================================================================
  VARIABLES
==============================================================================*/
body:before, body:after {
  position: fixed;
  height: 1.5em;
  line-height: 1.5em;
  font-size: 0.8em;
  top: 0;
  display: block;
  margin: 0 0;
  text-align: center;
}

body:before {
  z-index: 400;
  width: auto;
  padding: 0 4px;
  right: 0;
  background-color: rgba(255, 255, 255, 0.6);
  color: #000;
  content: attr(data-before);
}

body:after {
  z-index: 300;
  width: 100%;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  content: "greater than 1300px ";
}

@media (max-width: 1300px) {
  body:after {
    content: "at or below 1300px (desktop_xlarge)";
  }
}

@media (max-width: 1050px) {
  body:after {
    content: "at or below 1050px (desktop_large)";
  }
}

@media (max-width: 980px) {
  body:after {
    content: "at or below 980px (desktop_medium)";
  }
}

@media (max-width: 900px) {
  body:after {
    content: "at or below 900px (desktop_small)";
  }
}

@media (max-width: 800px) {
  body:after {
    content: "at or below 800px (desktop_xsmall)";
  }
}

@media (max-width: 769px) {
  body:after {
    content: "at or below 769px (desktop)";
  }
}

@media (max-width: 768px) {
  body:after {
    content: "at or below 768px (tablet)";
  }
}

@media (max-width: 767px) {
  body:after {
    content: "at or below 767px (below_tablet)";
  }
}

@media (max-width: 650px) {
  body:after {
    content: "at or below 650px (mobile_xlarge)";
  }
}

@media (max-width: 600px) {
  body:after {
    content: "at or below 600px (mobile_large)";
  }
}

@media (max-width: 550px) {
  body:after {
    content: "at or below 550px (mobile_medium)";
  }
}

@media (max-width: 414px) {
  body:after {
    content: "at or below 414px (mobile_small)";
  }
}

@media (max-width: 375px) {
  body:after {
    content: "at or below 375px (mobile_xsmall)";
  }
}

@media (max-width: 320px) {
  body:after {
    content: "at or below 320px (mobile_tiny)";
  }
}

